<script setup>
import Auth from "@/api/Auth";
import Laporan from "@/api/Laporan";
import { onBeforeMount, ref } from "vue";

const laporan = ref()
const user = ref()
const message = ref()

async function hapus(id) {
    if (confirm('Konfirmasi hapus laporan')) {
        const res = await Laporan.destroy(id)
        message.value = res.message
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        })

        setTimeout(() => {
            location.reload()
        }, 2000);
    }
}

onBeforeMount(async () => {
    const res = await Auth.find().catch((err) => err)
    user.value = res.username

    laporan.value = await Laporan.all()
})

</script>

<template>
    <h2>Laporan Keuangan</h2>

    <v-divider class="my-3"></v-divider>

    <v-alert type="success" class="my-3" closable close-label="Close Alert" v-if="message">
        {{ message }}
    </v-alert>

    <v-btn color="indigo" class="my-2" prepend-icon="mdi-plus" @click="$router.push('/add-laporan')" v-if="user">
        Tambah Laporan
    </v-btn>

    <template v-for="(item, i) in laporan" :key="i">
        <div>
            <v-list-item :title="item.bulan" :subtitle="(new Date(item.created)).toLocaleDateString()" class="py-3"
                :to="'/find-laporan/' + item._id">
                <template v-slot:prepend>
                    <v-avatar color="blue">
                        <v-icon color="white">mdi-clipboard-text</v-icon>
                    </v-avatar>
                </template>
            </v-list-item>

            <div class="border-b" v-if="user">
                <v-btn color="green" variant="text" :to="'/edit-laporan/' + item._id">Edit</v-btn>
                <v-btn color="red" variant="text" @click="hapus(item._id)">Hapus</v-btn>
            </div>
        </div>

    </template>
</template>